___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --gameboard-border-color: #bfdbfe;
  --ship-color: #60a5fa;
  --light-grey-color: #d1d5db;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

header {
  width: 100%;
  border-bottom: 2px solid silver;
  text-align: center;
}

header h1 {
  margin: 32px;
}

main {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.game-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  gap: 32px;
}

.game-message {
  grid-column: 1 / 3;
  font-weight: bold;
}

.player-container p{
  text-align: center;
}

.gameboard {
  width: 300px;
  height: 300px;
  display: flex;
  flex-direction: column;
  border: 2px solid var(--gameboard-border-color);
}

.gameboard.active {
  border-color: limegreen;
}

.row {
  flex: 1;
  display: flex;
}

.node {
  flex: 1;
  border: 1px solid var(--gameboard-border-color);
}

.active .node:hover {
  background-color: limegreen;
  border-color: limegreen;
}

.active .node.hit:hover {
  background-color: #ef4444;
  border-color: #ef4444;
}

.ship,
.hide-ship .ship.hit {
  background-color: var(--ship-color);
  border: 1px solid var(--ship-color);
}

.hide-ship .ship {
  background-color: inherit;
  border: 1px solid var(--gameboard-border-color);
}

.hit {
  position: relative;
  overflow: hidden;
}

.hit::after {
  position: absolute;
  content: '×';
  width: 100%;
  top: 50%;
  padding: 0;
  margin: 0;
  line-height: 0;
  text-align: center;
  font-size: 3rem;
}

.missed::after {
  color: var(--light-grey-color);
}

.ship.hit.sunk {
  background-color: grey;
  border: 1px solid grey;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,iCAAiC;EACjC,qBAAqB;EACrB,2BAA2B;AAC7B;;AAEA;EACE,sBAAsB;EACtB,SAAS;EACT,UAAU;EACV,yCAAyC;EACzC,iBAAiB;EACjB,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,WAAW;EACX,+BAA+B;EAC/B,kBAAkB;AACpB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,OAAO;EACP,WAAW;EACX,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,qBAAqB;EACrB,SAAS;AACX;;AAEA;EACE,kBAAkB;EAClB,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,aAAa;EACb,sBAAsB;EACtB,+CAA+C;AACjD;;AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,OAAO;EACP,aAAa;AACf;;AAEA;EACE,OAAO;EACP,+CAA+C;AACjD;;AAEA;EACE,2BAA2B;EAC3B,uBAAuB;AACzB;;AAEA;EACE,yBAAyB;EACzB,qBAAqB;AACvB;;AAEA;;EAEE,mCAAmC;EACnC,mCAAmC;AACrC;;AAEA;EACE,yBAAyB;EACzB,+CAA+C;AACjD;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA;EACE,kBAAkB;EAClB,YAAY;EACZ,WAAW;EACX,QAAQ;EACR,UAAU;EACV,SAAS;EACT,cAAc;EACd,kBAAkB;EAClB,eAAe;AACjB;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,sBAAsB;EACtB,sBAAsB;AACxB","sourcesContent":[":root {\n  --gameboard-border-color: #bfdbfe;\n  --ship-color: #60a5fa;\n  --light-grey-color: #d1d5db;\n}\n\nbody {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n  font-family: Arial, Helvetica, sans-serif;\n  min-height: 100vh;\n  display: flex;\n  flex-direction: column;\n}\n\nheader {\n  width: 100%;\n  border-bottom: 2px solid silver;\n  text-align: center;\n}\n\nheader h1 {\n  margin: 32px;\n}\n\nmain {\n  flex: 1;\n  width: 100%;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n.game-container {\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  justify-items: center;\n  gap: 32px;\n}\n\n.game-message {\n  grid-column: 1 / 3;\n  font-weight: bold;\n}\n\n.player-container p{\n  text-align: center;\n}\n\n.gameboard {\n  width: 300px;\n  height: 300px;\n  display: flex;\n  flex-direction: column;\n  border: 2px solid var(--gameboard-border-color);\n}\n\n.gameboard.active {\n  border-color: limegreen;\n}\n\n.row {\n  flex: 1;\n  di